.image-list {
  display: grid;
  /*auto-fill: grid system decides how many columns are needed 
   minmax: each column is going to be at minimum 250px and at most 1fr (maximum allocation fo space */
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 0 10px;
  grid-auto-rows: 10px;
}

.image-list img {
  width: 250px;
}
